.header-container {
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgb(0, 0, 0);
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px 5%;
  font-family: 'NanumSquare';
}
.inner-container{
  display: flex;
  justify-content: space-between;
}
.letter-frame{
  color: white;
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.login-button {
  background-color: #fff;
  color:#000;
  border: none;
  cursor: pointer;
  border-radius: 15px;
  padding: 10px;
}

@import url('https://cdn.rawgit.com/moonspam/NanumSquare/master/nanumsquare.css');