body {
    background-color: #22254b;
}

.app-container {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    color: white;
    font-family: 'NanumSquare';
}
.value-container {
    width: 1000px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-column-gap: 10px;
    grid-row-gap: 10px;
}
.value-container img {
    width: 100%;
    height: auto;
    opacity: 0;
    animation: slide-up 1s ease forwards;
}
.value-container img:hover {
    border-radius: 30px;
    background-color: rgb(84, 64, 121);
}
.leewonsuk{
    margin-top:50px;
    width: 1000px;
    opacity: 0;
    animation: slide-up 1s ease forwards;
}
.leewonsuk:hover {
    border-radius: 30px;
    background-color: rgb(84, 64, 121);
}
.kimhongki{
    margin-top:50px;
    width: 1000px;
    opacity: 0;
    animation: slide-up 1s ease forwards;
}
.kimhongki:hover {
    border-radius: 30px;
    background-color: rgb(84, 64, 121);
}

@keyframes slide-up {
    from {
    opacity: 0;
    transform: translateY(100%);
    }
    to {
    opacity: 1;
    transform: translateY(0);
    }
}
  
.value-container .value1 { animation-delay: 0.1s; }
.value-container .value2 { animation-delay: 0.2s; }
.value-container .value3 { animation-delay: 0.3s; }
.value-container .value4 { animation-delay: 0.4s; }
.value-container .value5 { animation-delay: 0.5s; }
.leewonsuk { animation-delay: 0.7s; }
.kimhongki { animation-delay: 0.9s; }